import axios from 'axios'

const api = axios.create({
  baseURL: 'http://localhost:5000/api', // Anpassen an dein Backend
  headers: {
    'Content-Type': 'application/json',
  },
})

type User = {
  id: number;
  email: string;
  password: string;
};

type Service = {
  id: number;
  name: string;
};

type Booking = {
  id: number;
  user: string; // E-Mail oder ID
  serviceId: number;
  dateTime: string;
};

// Dummy-DB
export const services: Service[] = [
  { id: 1, name: 'Haarschnitt' },
  { id: 2, name: 'Bart trimmen' },
  { id: 3, name: 'Massage' },
];

let bookings: Booking[] = [];
let bookingIdCounter = 1;

// Buchung erstellen
export function createBooking(user: string, serviceId: number, dateTime: string): Booking {
  const booking = {
    id: bookingIdCounter++,
    user,
    serviceId,
    dateTime,
  };
  bookings.push(booking);
  return booking;
}

// Alle Buchungen eines Users
export function getUserBookings(user: string): Booking[] {
  return bookings.filter((b) => b.user === user);
}

let users: User[] = []; // unser Fake-DB

let userIdCounter = 1;

export function registerUser(email: string, password: string): { success: boolean; message: string } {
  const exists = users.find(user => user.email === email);
  if (exists) {
    return { success: false, message: 'User already exists' };
  }
  users.push({ id: userIdCounter++, email, password });
  return { success: true, message: 'User registered successfully' };
}

export function loginUser(email: string, password: string): { success: boolean; message: string } {
  const user = users.find(u => u.email === email && u.password === password);
  if (!user) {
    return { success: false, message: 'Invalid credentials' };
  }
  return { success: true, message: 'Login successful' };
}

export default api
